.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #c2c2c2;
  font-weight: 600;
  text-transform: uppercase;
  overflow: scroll;

  &__top {
    margin-bottom: 20px;
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__content {
    @include contentPaddingInline;
  }

  &__phone-number {
    display: block;
    font-size: 16px;
    line-height: 27px;
    color: $second-text-color;
    text-decoration: none;
    margin-bottom: 16px;
  }

  &__call-to-order {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid $second-text-color;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: $second-text-color;
    text-decoration: none;
  }
}
